<template>
	<view class="container">
		<view class="page-body swiper-bottom">
			<view class='wrapper'>
				<scroll-view scroll-x class='toolbar sticky-box' @tap="format">
					<view :class="formats.bold ? 'ql-active' : ''" class="iconfont icon-zitijiacu" data-name="bold"></view>
					<view :class="formats.italic ? 'ql-active' : ''" class="iconfont icon-zitixieti" data-name="italic"></view>
					<view :class="formats.underline ? 'ql-active' : ''" class="iconfont icon-zitixiahuaxian" data-name="underline"></view>
					<view :class="formats.strike ? 'ql-active' : ''" class="iconfont icon-zitishanchuxian" data-name="strike"></view>
					<view :class="formats.align === 'left' ? 'ql-active' : ''" class="iconfont icon-zuoduiqi" data-name="align"
					 data-value="left"></view>
					<view :class="formats.align === 'center' ? 'ql-active' : ''" class="iconfont icon-juzhongduiqi" data-name="align"
					 data-value="center"></view>
					<view :class="formats.align === 'right' ? 'ql-active' : ''" class="iconfont icon-youduiqi" data-name="align"
					 data-value="right"></view>
					<view :class="formats.align === 'justify' ? 'ql-active' : ''" class="iconfont icon-zuoyouduiqi" data-name="align"
					 data-value="justify"></view>
					
					
					
					<view :class="formats.list === 'ordered' ? 'ql-active' : ''" class="iconfont icon-youxupailie" data-name="list"
					 data-value="ordered"></view>
					<view :class="formats.list === 'bullet' ? 'ql-active' : ''" class="iconfont icon-wuxupailie" data-name="list"
					 data-value="bullet"></view>
					<view class="iconfont icon-undo" @tap="undo"></view>
					<view class="iconfont icon-redo" @tap="redo"></view>

					<view class="iconfont icon-outdent" data-name="indent" data-value="-1"></view>
					<view class="iconfont icon-indent" data-name="indent" data-value="+1"></view>
					<view class="iconfont icon-fengexian" @tap="insertDivider"></view>
					<view class="iconfont icon-charutupian" @tap="insertImage"></view>
					<view class="iconfont icon-shanchu" @tap="clear"></view>
					

				</scroll-view>

				<editor id="editor" class="ql-container" :placeholder="placeholder" showImgSize showImgToolbar showImgResize
				 @statuschange="onStatusChange" :read-only="readOnly" @input="setData" @ready="onEditorReady">
				</editor>
			</view>
		</view>
		<!-- 底部 -->
		<view class="footer">
			<view class="submit-content go-indent">
				<text class="submit-red" @click="addGood()">保存</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
                readOnly: false,
				placeholder: '开始输入....',
				formats: {},
				getdata: ''
			}
		},
		methods: {
			readOnlyChange() {
				this.readOnly = !this.readOnly
			},
			onEditorReady() {
				uni.createSelectorQuery().select('#editor').context((res) => {
					this.editorCtx = res.context
					if(this.getdata){
						this.editorCtx.setContents({
							html:this.getdata  
						}) 
					}
				}).exec()
			},
			undo() {
				this.editorCtx.undo()
			},
			redo() {
				this.editorCtx.redo()
			},
			format(e) {
				let {
					name,
					value
				} = e.target.dataset
				if (!name) return
				// console.log('format', name, value)
				this.editorCtx.format(name, value)

			},
			onStatusChange(e) {
				const formats = e.detail
				this.formats = formats
			},
			insertDivider() {
				this.editorCtx.insertDivider({
					success: function() {
						console.log('insert divider success')
					}
				})
			},
			clear() {
				this.editorCtx.clear({
					success: function(res) {
						console.log("clear success")
					}
				})
			},
			removeFormat() {
				this.editorCtx.removeFormat()
			},
			insertDate() {
				const date = new Date()
				const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
				this.editorCtx.insertText({
					text: formatDate
				})
			},
			insertImage() {
				const that = this
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					success: (res) => {
						uni.uploadFile({
						  url: uni.getStorageSync('applyGoodSecret').host + 'uploadPictures',
						  filePath: res.tempFilePaths[0],
						  name: 'file',
						  header: {
							'apply-secret': uni.getStorageSync('applyGoodSecret').secret,
							'Authorization': 'Bearer ' + uni.getStorageSync('applytoken')
						  },
						  formData: {
							type: 1,
							size: 1024 * 500
						  },
						  success(res) {
							that.editorCtx.insertImage({
								src: res.data,
								alt: '图像',
								success: function() {
									
								}
							})
						  },
						  fail(res) {
							that.$api.msg(res.message);
							return false
						  }
						})
					}
				})
			},
			setData(e){
				this.getdata = e.detail.html
			},
			addGood(){
				this.$api.prePage().refreshDetails(this.getdata)
				this.$api.msg(`规格配置成功`);
				setTimeout(()=>{
					uni.navigateBack()
				}, 800)
			}
		},
		onLoad(option) {
			uni.loadFontFace({
				family: 'Pacifico',
				source: 'url("https://sungd.github.io/Pacifico.ttf")'
			})
			if(option.data){
				this.getdata = decodeURIComponent(option.data)
			}
		},
	}
</script>

<style lang="scss">
	@import "./editor-icon.css";
	.swiper-bottom{
		padding-bottom: 80upx;
	}
	.wrapper {
		padding: 5px;
	}

	.iconfont {
		display: inline-block;
		padding: 20upx 20upx;
		cursor: pointer;
		font-size: 50upx;
		
	}

	.toolbar {
		box-sizing: border-box;
		border-bottom: 0;
		font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
	}


	.ql-container {
		box-sizing: border-box;
		padding: 12px 15px;
		width: 100%;
		min-height: 30vh;
		height: auto;
		background: #fff;
		margin-top: 20px;
		font-size: 16px;
		line-height: 1.5;
	}

	.ql-active {
		color: #06c;
	}
	.sticky-box {
		/* #ifndef APP-PLUS-NVUE */
		display: flex;
		position: -webkit-sticky;
		/* #endif */
		position: sticky;
		top: var(--window-top);
		z-index: 99;
		flex-direction: row;
		margin: 0px;
		padding: 15px 0 15px 0;
		background-color: #F4F5F6;
		border-bottom-style: solid;
		border-bottom-color: #E2E2E2;
	}
	.footer{
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 995;
		display: flex;
		align-items: center;
		width: 100%;
		height: 90upx;
		justify-content: space-between;
		font-size: 30upx;
		background-color: #fff;
		z-index: 998;
		color: $font-color-base;
		box-shadow: 0 -1px 5px rgba(0,0,0,.1);
		.price-content{
			padding-left: 30upx;
		}
		.price-tip{
			color: $base-color;
			margin-left: 8upx;
		}
		.price{
			font-size: 36upx;
			color: $base-color;
		}
		.submit{
			display:flex;
			align-items:center;
			justify-content: center;
			width: 280upx;
			height: 100%;
			color: #fff;
			font-size: 32upx;
			background-color: $base-color;
		}
		.submit-content{
			display:flex;
			flex-direction:row-reverse;
			align-items:center;
			justify-content: center;
			height: 100%;
			.submit-red{
				display:flex;
				flex-direction:row-reverse;
				align-items:center;
				justify-content: center;
				width: 100%;
				height: 100%;
				color: #fff;
				font-size: 32upx;
				background-color: $base-color;
			}
			.submit-default{
				display:flex;
				align-items:center;
				justify-content: center;
				width: 200upx;
				height: 100%;
				color: #fff;
				font-size: 32upx;
				background-color: #909399;
			}
		}
		.go-indent{
			width: 100%;
		}
	}
</style>
